import React from "react";
import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormGroup from "@material-ui/core/FormGroup";
import Checkbox from "@material-ui/core/Checkbox";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Chip from "@material-ui/core/Chip";
import Grid from "@material-ui/core/Grid";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import GitHubIcon from "@material-ui/icons/GitHub";
import Footer from "./Footer";
import SlideShow from "slideshow";
import { useStyles } from "./styles";
import { AppStateContext } from "./AppState";

const images = [
    {
        id: "10",
        label: "Keel-billed toucan",
        path:
            "https://upload.wikimedia.org/wikipedia/commons/f/f6/Keel-billed_Toucan_%2816201157519%29.jpg"
    },
    {
        id: "20",
        label: "Rainbow_Lorikeet.",
        path:
            "https://upload.wikimedia.org/wikipedia/commons/a/a8/Rainbow_Lorikeet_%28Trichoglossus_moluccanus%29_at_Adelaide_Airport_1.jpg"
    },
    {
        id: "30",
        label: "Australian ringneck",
        path:
            "https://upload.wikimedia.org/wikipedia/commons/thumb/e/e3/Mallee_ringneck_42_-_Patchewollock.jpg/1920px-Mallee_ringneck_42_-_Patchewollock.jpg"
    },
    {
        id: "40",
        label: "Rainbow Lorikeet",
        path:
            "https://upload.wikimedia.org/wikipedia/commons/4/4b/Atlantic_Puffin_Fratercula_arctica.jpg"
    },
    {
        id: "50",
        label: "Rainbow_Lorikeet.",
        path:
            "https://upload.wikimedia.org/wikipedia/commons/e/e0/BlueAndYellowMacaw_AraArarauna.jpg"
    },
    {
        id: "60",
        label: "Rainbow_Lorikeet.",
        path:
            "https://upload.wikimedia.org/wikipedia/commons/1/13/Trichoglossus_moluccanus_with_open_wings%2C_Brisbane.jpg"
    }
];

export default function SlideShowComponent() {
    const classes = useStyles();

    const store = React.useContext(AppStateContext);

    const sizeRef = React.useRef();
    const [size, setSize] = React.useState(800);
    const [containerRatio, setContainerRatio] = React.useState(1.5);
    const [containerShadow, setContainerShadow] = React.useState(true);
    const [imageShadow, setImageShadow] = React.useState(true);
    const [showEdges, setShowEdges] = React.useState(true);

    const handleSubmit = (event) => {
        event.preventDefault();
        if (sizeRef.current.value.includes("%")) setSize(sizeRef.current.value);
        if (sizeRef.current.value.includes("px")) {
            let newValue = sizeRef.current.value.slice(0, -2);
            setSize(+newValue);
        } else setSize(+sizeRef.current.value);
    };

    return (
        <div className={classes.root}>
            <Box className={classes.title}>
                <Box
                    className={classes.buttonLink}
                    to="/"
                    component={RouterLink}
                >
                    <ArrowBackIcon color="secondary" />
                </Box>
                <Typography variant="h3" align="center">
                    A Carousel component with a sliding
                </Typography>
            </Box>

            <Grid container className={classes.rootPapper}>
                <Grid
                    item
                    sm={12}
                    md={12}
                    lg={6}
                    className={classes.PaperChips}
                >
                    <Chip label="React" color="primary" />
                    <Chip label="UseSpring" color="primary" />
                    <Chip label="UseGesture" color="primary" />
                    <Chip label="Material UI" color="primary" />
                </Grid>
                <Grid item xs={12} sm={10} md={8} lg={7}>
                    <div
                        className={
                            store.currentTheme.palette.type === "light"
                                ? classes.formControl
                                : classes.formControlDark
                        }
                    >
                        <form
                            onSubmit={handleSubmit}
                            className={classes.formRoot}
                            noValidate
                            autoComplete="off"
                        >
                            <TextField
                                id="outlined-basic"
                                select
                                SelectProps={{
                                    native: true
                                }}
                                size="small"
                                name="ratio"
                                value={containerRatio}
                                onChange={(event) => {
                                    setContainerRatio(event.target.value);
                                }}
                                label="Ratio"
                                variant="outlined"
                            >
                                {[1.2, 1.5, 1.8].map((option, index) => (
                                    <option key={index} value={option}>
                                        {option}
                                    </option>
                                ))}
                            </TextField>
                            <FormControl>
                                <TextField
                                    inputRef={sizeRef}
                                    id="outlined-basic"
                                    size="small"
                                    name="size"
                                    label="Width"
                                    defaultValue="800"
                                    variant="outlined"
                                />
                            </FormControl>
                            <Button
                                variant="outlined"
                                color="secondary"
                                onClick={handleSubmit}
                            >
                                ok
                            </Button>
                        </form>

                        <FormGroup className={classes.formGr} row>
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={containerShadow}
                                        onChange={() =>
                                            setContainerShadow(!containerShadow)
                                        }
                                        name="container-shadow"
                                        inputProps={{
                                            "aria-label": "primary checkbox"
                                        }}
                                    />
                                }
                                label="Container Shadow"
                            />
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={imageShadow}
                                        onChange={() =>
                                            setImageShadow(!imageShadow)
                                        }
                                        name="Image Shadow"
                                    />
                                }
                                label="Image Shadow"
                            />
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={showEdges}
                                        onChange={() =>
                                            setShowEdges(!showEdges)
                                        }
                                        name="show-edges"
                                    />
                                }
                                label="Show Next/Prev images"
                            />
                        </FormGroup>
                    </div>
                    <Box mt={3} display="flex" alignItems="center">
                        <GitHubIcon color="secondary" />
                        <Typography className={classes.typoLink}>
                            <Link
                                href="https://github.com/irishakarpova/carousel-sliding-effect"
                                variant="body2"
                            >
                                GitHub
                            </Link>
                        </Typography>
                    </Box>
                </Grid>
            </Grid>

            <div className={classes.rootSlider}>
                <SlideShow
                    serverData={images}
                    containerMaxWidth={size}
                    containerRatio={containerRatio}
                    containerShadow={containerShadow}
                    imageShadow={imageShadow}
                    showNextPrev={showEdges}
                />
            </div>
            <Footer />
        </div>
    );
}
